import React from 'react';
import PropTypes from 'prop-types';

import {
	Navigation, Row, Col, Button, Group,
	Form, Input, Radio, Checkbox, Menu,
	Modal, A, Icon,
} from '../../../components';

import { toString } from '../../utils/objectUtil';
import Select from "../../../components/Form/Select";

class GroupPage extends React.Component {
	constructor() {
		super();
		this.state = {
			direct: 'right',
		};
	}

	render() {
		return (
			<div>
				<h1></h1>
				<p>
					
					<A href="http://fontawesome.io" target="_blank">Font Awesome</A>
					
						<A href="http://iconfont.cn/" target="_blank">iconfont</A>
					
				</p>

				<h2></h2>
				<p>
					Bamboo UIFont Awesome css
				</p>
				<pre className="code">
					{'<link href="[]/font-awesome.min.css" rel="stylesheet">'}
				</pre>

				<h2></h2>
				<p>
					<code>Icon</code><code>name</code>
					<code>spin</code>
				</p>
				<div className="measurement">
					<div className="preview">
						<Icon name="smile-o" />
						{' '}
						<Icon name="refresh" spin />
					</div>
					<pre className="code">
						{`
<Icon name="smile-o" />
<Icon name="refresh" spin />
`.trim()}
					</pre>
				</div>

				<h2></h2>
				<p>
					
					<A href="http://fontawesome.io" target="_blank">Font Awesome</A>
					
					
					<code>Icon.Loading</code>
				</p>
				<div className="measurement">
					<div className="preview">
						<Icon.Loading />
						{' '}
						<Icon.Loading id={1} />
						{' '}
						<Icon.Loading id={2} />
						{' '}
						<Icon.Loading id={3} />
						{' '}
						<Icon.Loading id={4} />
					</div>

					<pre className="code">
						{`
<Icon.Loading />
<Icon.Loading id={1} />
<Icon.Loading id={2} />
<Icon.Loading id={3} />
<Icon.Loading id={4} />
`.trim()}
					</pre>
				</div>

				<p>
					<code>Icon.Caret</code>
				</p>
				<div className="measurement">
					<div className="preview">
						
						<Icon.Caret direct={this.state.direct} />
						
					</div>

					<div className="form">
						<Form instance={this}>
							<Form.Field name="direct">
								{['right', 'left', 'up', 'down'].map(direct => (
									<Radio value={direct} key={direct}>
										{direct}
									</Radio>
								))}
							</Form.Field>
						</Form>
					</div>

					<pre className="code">
						{`<Icon.Caret direct="${this.state.direct}" />`}
					</pre>
				</div>

				<p>
					
				</p>
				<div className="measurement">
					<div className="preview">
						<Icon.Search />
					</div>

					<pre className="code">
						{'<Icon.Search />'}
					</pre>
				</div>
			</div>
		);
	}
}

GroupPage.propTypes = {
	history: PropTypes.object,
};

export default GroupPage;



